.App {
  text-align: center;
}
.inputwala{
  width: 400px;
  padding: 100px;
  box-shadow: 0px 0px 10px black;
  margin: 100px auto;
  display: flex;

}
.inputwala input{
  width: 350px;
  border-radius: 5px;
  padding: 10px;
  margin-right: 10px;
}
.inputwala button{
  width: 100px;
  padding: 10px;
  font-size: 15px;
  background-color: #46a6c1;
  border: none;
  color: white;
  font-weight: 600;
  border-radius: 5px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
